
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'memberFamily',
	mixins: [mixins.scroller],
	data() {
		return {
			show: false				
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetPatientSignList',
				},
				body: {
					DoctorID: this.$common.getLocalStorage('user').doctorid,
					CheckState:'1',
				}
			};
		}
	},
	methods: {			
		onHide(opt) {
			this.show = false;
			this.$emit('on-hide', opt);
		}
	},
	mounted() {},
	components: {
		XDialog,
		Spinner
	}
}



