
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import sha512 from 'js-sha512' //sha512加密
import mixins from 'assets/js/mixins.js'	
export default {
	name:'login',
	mixins: [mixins.city],
	data() {
		return {
			tel: '',
			password: '',
			cityList: [] //城市三级联动数据，来自数据库下载来的json文件
		}
	},
	computed: {
		btnType() {
			return this.$validator.errorBag.any() ? 'default' : 'primary'
		}
	},
	methods: {
		//登录
		login() {
			this.$common.login({
					loading:true,
					tel: this.tel,
					password: sha512(this.password),
					render: () => {
						this.$router.push({
							path: '/homeRouterView/home'
						});
						if(!localStorage.cityList) {
							this.getCityList();
						}
					}
				})
		},
		
	},
	mounted() {
		this.$validator.validateAll()
		if(localStorage.getItem('token')) {
			this.$router.push({
				path: '/homeRouterView/home'
			});
		} else {
			this.$router.push({
				path: '/login'
			});
		}
		
	},
	components: {
		XInput,
		Group,
		XButton,
		XHeader
	}
}



