
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
	name: 'healthInterventionProgramDetails',
	data() {
		return {
			details: {},
		}
	},
	methods: {
		getData() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetHealthSuggestByID',
					},
					body: {
						SuggestID: this.$route.query.id
					}
				},
				success: (res) => {
					this.details = res
				}
			})
		},
		editHealthSuggest() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认提交信息？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'EditHealthSuggest',
							},
							body: {
								SuggestID: this.$route.query.id,
								Content: this.details.Content
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '修改成功',
								onHide: () => {
									this.$router.go(-1);
								}
							})
						}
					})
				}
			})

		}
	},
	mounted() {
		this.getData();
		
	},

	components: {
		XHeader,
		XTextarea

	}
}



