
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import xhFoot from 'components/footer';
export default {
	data() {
		return {
			eqiupBind: [], //设备绑定列表
			details: {}, //用户信息
			follow: {
				loading: true,
				count: ''
			},
			guardian: {
				loading: true,
				count: ''
			}
		}
	},
	computed: {
		//绑定状态
		eqiupState() {
			return this.eqiupBind.length > 0 ? true : false
		}
	},
	methods: {			
		//获取个人资料
		getData() {
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetDoctorInfo',
					},
					body: {
						DoctorID: this.$common.getLocalStorage('user').doctorid
					}
				},
				success: (res) => {
					this.details = res;
					this.$common.setLocalStorage('realState', this.details.RealState);
				}
			})
		},			
		//解除绑定
		logout() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认解除绑定？',
				onConfirm: () => {
					this.$common.ajax({
						loadingText: '解除中',
						data: {
							header: {
								action: 'WxUnbind',
							},
							body: {
								usertype:0,
								openid: localStorage.openId
							}
						},
						success: (res) => {	
							this.$common.clearLocalStorage();
							this.$router.push({
								path: '/login'
							});
						}
					})
				}
			})
		}
	},
	mounted() {
		this.getData();		
	},

	components: {
		Group,
		Cell,
		xhFoot,
		XHeader,
		XButton,
		Spinner
	}
}



