
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import mixins from 'assets/js/mixins.js'
import dateTime from 'components/dateTime'
export default {
	name: 'appointment',
	mixins: [mixins.scroller],
	data() {
		return {
			visitState: [{
				name: '全部',
				value: ''
			},{
				name: '未就诊',
				value: 0
			}, {
				name: '已就诊',
				value: 1
			}, {
				name: '过期',
				value: 2
			}],
			visitValue: [],
			state: {
				0: 'warn', //未就诊
				1: 'success', //已就诊
				2: 'overdue' //过期
			}
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetAppointmentList',
				},
				body: {
					DoctorID: this.$common.getLocalStorage('user').doctorid,
					State:this.visitValue[0]
				}
			}
		}
	},
	methods: {			
	},
	mounted() {},
	components: {
		XHeader,
		Spinner,
		dateTime,
		PopupPicker
	}
}



